import './styles/main.scss';

const menuToggle = document.querySelector('[data-js=menu-toggle]');
const menuNav = document.querySelector('[data-js=menu-nav]');


menuToggle &&
	menuToggle.addEventListener('click', toggleMenu);
menuNav && menuNav.addEventListener('click', scrollToElement);

//toggle menu 
function toggleMenu() {
	menuNav.classList.toggle('open');
	menuToggle.classList.toggle('close');
}
//scroll to element that is clicked 
function scrollToElement(event) {
	const eleToScrollTo = document.querySelector(`#${event.target.textContent}`);
	if (!eleToScrollTo || eleToScrollTo.innerText == 'CALL NOW') {
		return;
	} else {
		event.preventDefault();
		toggleMenu();
		removeActiveClass(menuItems)
		event.target.classList.toggle('active');
		eleToScrollTo.scrollIntoView({
			behavior: 'smooth',
			block: 'start',
			inline: 'start'
		});
	}
}
const menuItems = Array.from(document.querySelectorAll('.nav_list-item--link'));

function removeActiveClass(arr) {
	arr.forEach(element => {
		if (element.className.includes('active')) {
			element.classList.remove('active');
		}
		return;
	});
}